// @flow
import * as React from 'react';
import {UserActionChain} from "../../../proto/framework/analytics/AnalyticMessage";
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import {Stack, StackProps} from "@mui/material";

import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import {Typography} from "../../../component/wrapper/MuiWrappers";
import ProfileAvatar from "../../customer/component/ProfileAvatar";
import ListComponent from "../../../component/container/ListComponent";
import UserActionViewer from "./UserActionViewer";
import {get_customer_url} from "../../../navigation/routerUtil";

type Props = {
  userActionChain: UserActionChain;
} & StackProps;

const UserActionChainViewer = ({userActionChain, sx, ...rest}: Props) => {

  const start = new Date(userActionChain.actions[0].actionTime);
  const end = new Date(userActionChain.actions[userActionChain.actions.length - 1].actionTime);
  const total = userActionChain.actions.length;

  return <Stack width={"100%"} sx={{...sx}} {...rest}>
    <Accordion variant={"outlined"}  slotProps={{ transition: { timeout: total > 10 ? 0 : 200 }}}>
      <AccordionSummary
          expandIcon={<ArrowDownwardIcon/>}
          aria-controls="panel1-content"
          id="panel1-header"
      >
        <Stack direction={"row"} alignItems={"center"} spacing={2}>
          <ProfileAvatar title={userActionChain.customer?.name}
                         profileLink={get_customer_url(userActionChain.customer?.id)}
                         avatarUrl={userActionChain.customer?.avatarUrl}/>
          <Typography justifyContent={"start"}  enableTranslate={false}
          > {userActionChain.customer?.name}:</Typography>
          <Typography
              justifyContent={"start"}  enableTranslate={false}> {start.toLocaleString()} - {end.toLocaleString()}</Typography>
          <Typography
              justifyContent={"start"} enableTranslate={false}> {userActionChain.actions.length} </Typography>
        </Stack>
      </AccordionSummary>
      <AccordionDetails>
        <Stack direction={"column"} spacing={2}>
          <ListComponent data={userActionChain.actions} renderItem={(action, index) => {
            return <UserActionViewer userAction={action} key={index}/>;
          }}/>
        </Stack>
      </AccordionDetails>
    </Accordion>

  </Stack>
};

export default UserActionChainViewer;