import * as React from 'react';
import {Stack, StackProps} from "@mui/material";
import dayjs, {Dayjs} from 'dayjs';
import {DateTimePicker} from '@mui/x-date-pickers/DateTimePicker';
import {useTranslation} from "react-i18next";
import {Button, LoadingButton, TextField} from "../../component/wrapper/MuiWrappers";
import {queryUserAction} from "./AnalyticService";
import {UserActionChain} from "../../proto/framework/analytics/AnalyticMessage";
import AlertContext from "../../context/AlertContext";
import ListComponent from "../../component/container/ListComponent";
import UserActionChainViewer from "./component/UserActionChainViewer";
import {print} from "../../api/util/Logging";
import {useAppSelector} from "../../api/redux/hooks";
import {selectLoginResponse} from "../../api/redux/slice/SettingSlice";

type AnalyticWidgetProps = {} & StackProps;
const AnalyticWidget = ({sx, ...rest}: AnalyticWidgetProps) => {
  const {t} = useTranslation();
  const loginResponse = useAppSelector(selectLoginResponse);
  const {showAlert} = React.useContext(AlertContext);
  const [startDate, setStartDate] = React.useState<Dayjs | null>(dayjs('2024-01-01T00:00'));
  const [endDate, setEndDate] = React.useState<Dayjs | null>(dayjs());
  const [intervalMinutes, setIntervalMinutes] = React.useState<number>(5);
  const [loading, setLoading] = React.useState<boolean>(false);
  const [customerIds, setCustomerIds] = React.useState<string>(loginResponse?.userId || "");

  const [userActionChains, setUserActionChains] = React.useState<UserActionChain[]>([]);

  const query = () => {
    setLoading(true);
    const customerIdsArray = customerIds.length > 0 ? customerIds.split(",").map((id) => id.trim()) : [];
    queryUserAction(customerIdsArray, startDate?.valueOf(), endDate?.valueOf(), intervalMinutes * 60 * 1000).then((res) => {
      setUserActionChains(res.userActionChains);
    }).catch((e) => {
      showAlert(e.message, "error");
    }).finally(() => {
      setLoading(false);
    });
  }

  return <Stack direction={"column"} spacing={2} sx={{...sx}} {...rest}>
    <Stack direction={{
      xs: 'column',
      md: 'row',
    }} spacing={{
      xs: 1,
      md: 2,
    }} justifyContent={"center"}>
      <TextField
          label="Customer IDs"
          type="text"
          variant="outlined"
          placeholder={"id1, id2,..."}
          value={customerIds}
          onChange={(e) => setCustomerIds(e.target.value)}
      />

      <DateTimePicker
          label={t("Start Date")}
          value={startDate}
          onChange={(newValue) => setStartDate(newValue)}
      />

      <DateTimePicker
          label={t("End Date")}
          value={endDate}
          onChange={(newValue) => setEndDate(newValue)}
      />

      <TextField
          label="Interval (minutes)"
          type="number"
          variant="outlined"
          value={intervalMinutes}
          onChange={(e) => setIntervalMinutes(parseInt(e.target.value))}
      />

      <LoadingButton loading={loading}
                     variant={"contained"}
                     onClick={query}

      >{"Query"}</LoadingButton>
    </Stack>

    <Stack direction={"column"} spacing={1}>
      <ListComponent data={userActionChains} renderItem={(item, index) => {
        return <UserActionChainViewer userActionChain={item} key={index}/>
      }}/>
    </Stack>

  </Stack>
};

export default AnalyticWidget;