// @flow
import * as React from 'react';
import {UserActionChain_UserAction} from "../../../proto/framework/analytics/AnalyticMessage";
import {Divider, Stack, StackProps} from "@mui/material";
import {Typography} from "../../../component/wrapper/MuiWrappers";
import SubChannelViewer from "./SubChannelViewer";

type Props = {
  userAction: UserActionChain_UserAction;
} & StackProps;
const UserActionViewer = ({userAction, sx, ...rest}: Props) => {
  return <Stack direction={"column"} width={"100%"} spacing={1} sx={{...sx}} {...rest}>
    <Stack direction={"row"} justifyContent={"flex-start"} spacing={1} alignItems={"center"}>
      <SubChannelViewer subChannel={userAction.subChannel} fontSize={"large"}/>
      <Typography
          enableTranslate={false} variant={"h6"}>{new Date(userAction.actionTime).toLocaleString()}</Typography>
      <Typography enableTranslate={false} variant={"caption"}>{userAction.sessionId}</Typography>
    </Stack>
    <Stack direction={"row"} justifyContent={"flex-start"} spacing={1}>
    <Typography enableTranslate={false} enableLinkify={true} textAlign={"left"}>{userAction.action}</Typography>
    </Stack>
    <Divider/>
  </Stack>
};

export default UserActionViewer;