import {UserActionQuery, UserActionResponse} from "../../proto/framework/analytics/AnalyticMessage";
import {pb_pbPost} from "../../api/util/MyDimensionClient";
import {TimeRange} from "../../proto/framework/QueryMessage";

const aiChatAPIPrefix = '/api/v0/admin';

export const queryUserAction = (userIds: string[] = [], start?: number, end?: number, interval = 5 * 60 * 1000) => {
  const userActionQuery = UserActionQuery.create();
  const timeRange = TimeRange.create();
  userActionQuery.userId = userIds;
  if (start) {
    timeRange.start = start;
  }
  if (end) {
    timeRange.end = end;
  }
  userActionQuery.time = timeRange;
  userActionQuery.interval = interval;
  return pb_pbPost(aiChatAPIPrefix + "/query_user_action", UserActionQuery, UserActionResponse, userActionQuery);

}