// @flow
import * as React from 'react';
import {SvgIconProps} from "@mui/material/SvgIcon";
import ApiIcon from '@mui/icons-material/Api';
import AccessibilityNewIcon from '@mui/icons-material/AccessibilityNew';
import ListAltIcon from '@mui/icons-material/ListAlt';
import RouteIcon from '@mui/icons-material/Route';
import PowerIcon from '@mui/icons-material/Power';
import {Tooltip} from "../../../component/wrapper/MuiWrappers";

type SubChannelViewerProps = {
  subChannel: string;
} & SvgIconProps;

const SubChannelViewer = ({subChannel, ...rest}: SubChannelViewerProps) => {
  const getIcon = (subChannel: string) => {
    switch (subChannel) {
      case "api_call":
        return <ApiIcon {...rest}/>;
      case "dialog":
        return <ListAltIcon {...rest}/>;
      case "route":
        return <RouteIcon {...rest}/>;
      case "websocket":
        return <PowerIcon {...rest}/>;
      default:
        return <AccessibilityNewIcon {...rest}/>;
    }
  }
  return <Tooltip title={subChannel} enableTranslate={false}>{getIcon(subChannel)}</Tooltip>;
};

export default SubChannelViewer;
